body {
  background-color: #1f2125;
}


.App {
  
  width: 350px;
  height: 700px;
  overflow: hidden;
  margin: 30px auto;
  border-radius: 30px;
  background-color: #181a1e;
  color: #deebf8;
  position: relative;
  display: grid;
  grid-template-rows: 50% auto auto;
}
.info {
  text-align: center;
}
.info > img {
  width: 100%;
}

.info > h3 {
  margin: 5px;
}

.info a {
  text-decoration: none;
  color: inherit;
}

.info--occu {
  color: #5e4f49;
}

/* CSS */
.button-1,
.button-2 {
  align-items: center;
  background-color: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0.25rem;
  box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0.85);
  cursor: pointer;
  display: inline-flex;
  font-family: system-ui, -apple-system, system-ui, "Helvetica Neue", Helvetica,
    Arial, sans-serif;
  font-size: 16px;
  font-weight: 600;
  justify-content: center;
  line-height: 1.25;
  margin: 0;
  min-height: 3rem;
  padding: calc(0.875rem - 1px) calc(1.5rem - 1px);
  position: relative;
  text-decoration: none;
  transition: all 250ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  width: auto;
  margin: 10px;
}

.button-1:hover,
.button-1:focus,
.button-2:hover,
.button-2:focus {
  border-color: rgba(0, 0, 0, 0.15);
  box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
  color: rgba(0, 0, 0, 0.65);
}

.button-1:hover,
.button-2:hover {
  transform: translateY(-1px);
}

.button-1:active,
.button-2:active {
  background-color: #f0f0f1;
  border-color: rgba(0, 0, 0, 0.15);
  box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
  color: rgba(0, 0, 0, 0.65);
  transform: translateY(0);
}

button > img {
  margin-right: 10px;
  width: 20px;
}
.ai {
  padding-left: 20px;
  padding-right: 20px;
}

.ai h3 {
  margin: 10px;
}

.txt {
  padding-left: 5px;
  margin: 0 5px;
  color: #585a5f;
}

footer {
  position: absolute;
  display: block;
  bottom: 0;
  width: 100%;
  height: 50px;
  /* padding-left: 40px; */
  /* padding-right: 40px; */
  /* padding: 10px 40px; */
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  align-content: center;
  background-color: #151517;
}

footer a {
  text-decoration: none;
  margin: auto;
  width: 40px;
}

footer a img {
  width: 100%;
}
